.new{

	display: flex;
	flex-direction: column;
	gap: $size-l3;

	&__container{

		display: flex;
		flex-direction: column;
		gap: $size-r;

	}

	&__title{

		font:{

			family: $font-tomorrow;
			size: $size-l1;
			weight: 700;

		}

	}

	&__img{

		width: 100%;
		max-width: 840px;
		max-height: 840px;

		img{

			width: 100%;
			height: 100%;
			object-fit: cover;

		}

		figcaption{

			font:{

				size: $size-s3;
				weight: 100;
				style: italic;

			}

		}

	}

	&__subtitle{

		font:{

			size: $size-r;
			weight: 100;
			style: italic;

		}
		color: $color-c1;

	}

	&__paragraph{

		font:{

			size: $size-r * .8;
			weight: 100;

		}

	}

	.button{

		display: flex;
		justify-content: center;
		width: 100%;

		&__container{

			width: $size-r * 11;
			display: flex;
			align-items: center;
			justify-content: center;
			gap: $size-s2;
			border: none;
			outline: .1rem solid $color-e1;
			background-color: transparent;
			border-radius: $size-l2;
			padding: $size-s2 $size-r;

		}

		&__text{

			font-size: $size-r;
			color: $color-e1;

		}

		svg{

			color: $color-e1;
			font:{


				size: $size-l2;

			}

		}

	}

}